<template>  
  <form class='ion-padding' @submit.prevent="submitForm">
    <ion-list>
        <ion-item>
            <ion-label position="floating">Title</ion-label>
            <ion-input type="text" required v-model="enteredTitle"/>
        </ion-item>
        <ion-item>
            <ion-thumbnail slot="start">
                <img :src="takenImageURL" alt="Preview of the image"/>
            </ion-thumbnail>
            <ion-button type="button" fill="clear" @click="takePhoto">
                <ion-icon slot="start" :icon="camera"/>
                Take Photo
            </ion-button>
        </ion-item>
        <ion-item>
            <ion-label position="floating">Description</ion-label>
            <ion-textarea rows="5" v-model="enteredDescription"></ion-textarea>
        </ion-item>
    </ion-list>
    <ion-button expand="block" type="submit">Save</ion-button>
  </form>
</template>

<script>
import { IonButton, IonIcon, IonItem, IonInput, IonLabel, IonList, IonTextarea, IonThumbnail} from '@ionic/vue'
import { camera } from 'ionicons/icons'
import { CameraResultType, CameraSource, Plugins } from '@capacitor/core'

const { Camera } = Plugins;

export default {
    emits: ['save-memory'],    
    components: {
        IonButton,
        IonIcon,
        IonInput,
        IonItem,
        IonLabel,
        IonList,
        IonTextarea,
        IonThumbnail,
    },
    data() {
        return {
            camera,
            enteredTitle: '',
            enteredDescription: '',
            takenImageURL: null,
        }
    },
    methods: {
        submitForm() {
            const memoryData = {
                title: this.enteredTitle,
                image: this.takenImageURL,
                description: this.enteredDescription,
            }
            this.$emit('save-memory', memoryData)
        },
        async takePhoto() {
            const photo = await Camera.getPhoto({
                resultType: CameraResultType.Uri,
                source: CameraSource.Camera,
                quality: 60,
            })
            this.takenImageURL = photo.webPath
        }
    }
}
</script>

